import React from 'react';
import ReactDOM from 'react-dom';
import Button from '@material-ui/core/Button';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

import './index.css';

class Page extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            children: props.children,
            id: 0
        };

        this.handleClick = this.handleClick.bind(this);
        this.setID = this.setID.bind(this);
    }

    setID(id_in){
        this.setState({id: id_in});
    }

    handleClick(action) {
        let asArray = React.Children.toArray(this.state.children);

        if (action.remove) {
            const index = this.state.children.findIndex(a => a.type.name === action.remove);
            asArray.splice(index, 1);
        }

        if (action.add) {
            asArray.push(action.add);
        }

        this.setState({children: asArray})
    }

    render() {
        const childrenWithProps = React.Children.map(this.state.children, child =>
            React.cloneElement(child, { handleClick: this.handleClick, id: this.state.id, setID: this.setID })
        );

        return (
            <div className="page">
                {childrenWithProps}
            </div>
        )
    }
}


class MidiPlayer extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            link: "https://s3-us-west-2.amazonaws.com/midigenes.generated/output" + props.id + ".mid",
            ready: false
        };
        let request = new XMLHttpRequest();
        let self = this;

        request.open('GET', self.state.link);
        request.send();
        request.onload = function() {
            if (request.status !== 200) {
                setTimeout(function(){
                    request.open('GET', self.state.link);
                    request.send();
                }, 2000);
            } else {
                self.setState({ready: true})
            }
        };
    }

    render(){
        return(
            <div>
                {this.state.ready
                    ? <a href={this.state.link}>Download</a>
                    : <p>Generating...</p>
                }
                <br/><br/>
                <Button variant="text"
                        onClick={() => this.props.handleClick({
                            remove: "MidiPlayer",
                            add: <Links />})}>Home</Button>
                <br/>
            </div>
        )
    }
}
class MidiGenes extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            note: 'C',
            scale: 'major',
            bpm: 120,
            pop_size: 2500,
            epochs: 5000,
            mut_rate: 0.05,
            id: props.id
        };
        let link = "https://s3-us-west-2.amazonaws.com/midigenes.generated/output" + this.state.id + ".mid"
        let self = this
        let request = new XMLHttpRequest();
        request.open('GET', link);
        request.send();
        request.onload = function() {
            if (request.status !== 200) {
            } else {
                self.setState({id: self.state.id+1})
                link = "https://s3-us-west-2.amazonaws.com/midigenes.generated/output" + self.state.id + ".mid"
                request.open('GET', link);
                request.send();
            }
        };

        this.handleChange = this.handleChange.bind(this);
        this.callScript = this.callScript.bind(this);


    }

    callScript() {
        fetch('https://ngtdt9lmch.execute-api.us-west-2.amazonaws.com/test/midiCaller',{
            method: 'POST',
            body: JSON.stringify(this.state)

        }).then(response => {
            console.log(response)

        })
            .catch(error =>{
                console.log(error)
            })
        this.props.setID(this.state.id)
        this.props.handleClick({
            remove: "MidiGenes",
            add: <MidiPlayer />});
    }



    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    render() {
        return (
            <div>
                <h1>Midi-Genes</h1>
                <p><i>Large epoch and population sizes will require more time to process,
                    <br/>but will generally yield "better" results. <br/>
                    <br/>Please wait until your request has finished before making another.
                </i></p>
                <br/><br/>
                <form className={"midiform"} autoComplete="off">

                    {/*     NOTE     */}
                    <FormControl className={"midiform.formControl"}>
                        <InputLabel htmlFor="note">Note</InputLabel>
                        <Select
                            value={this.state.note}
                            onChange={this.handleChange}
                            inputProps={{
                                name: 'note',
                                id: 'note',
                            }}
                        >
                            <MenuItem value={"C"}>C</MenuItem>
                            <MenuItem value={"C#"}>C#</MenuItem>
                            <MenuItem value={"D"}>D</MenuItem>
                            <MenuItem value={"E"}>E</MenuItem>
                            <MenuItem value={"F"}>F</MenuItem>
                            <MenuItem value={"F#"}>F#</MenuItem>
                            <MenuItem value={"G"}>G</MenuItem>
                            <MenuItem value={"G#"}>G#</MenuItem>
                            <MenuItem value={"A"}>A</MenuItem>
                            <MenuItem value={"A#"}>A#</MenuItem>
                            <MenuItem value={"B"}>B</MenuItem>
                        </Select>
                    </FormControl>

                    {/*     SCALE     */}
                    <FormControl className={"midiform.formControl"}>
                        <InputLabel htmlFor="scale">Scale</InputLabel>
                        <Select
                            value={this.state.scale}
                            onChange={this.handleChange}
                            inputProps={{
                                name: 'scale',
                                id: 'scale',
                            }}
                        >
                            <MenuItem value={"major"}>Major</MenuItem>
                            <MenuItem value={"minor"}>Minor</MenuItem>
                        </Select>
                    </FormControl>

                    {/*     BPM     */}
                    <FormControl className={"midiform.formControl"}>
                        <InputLabel htmlFor="bpm">BPM</InputLabel>
                        <Input
                            value={this.state.bpm}
                            onChange={this.handleChange}
                            inputProps={{
                                name: 'bpm',
                                id: 'bpm',
                                step: 1,
                                min: 30,
                                max: 300,
                                type: 'number',
                                'aria-labelledby': 'input-slider',
                            }}
                        />
                    </FormControl>

                    {/*     POP_SIZE     */}
                    <FormControl className={"midiform.formControl"}>
                        <InputLabel htmlFor="pop_size">Population</InputLabel>
                        <Input
                            value={this.state.pop_size}
                            onChange={this.handleChange}
                            inputProps={{
                                name: 'pop_size',
                                id: 'pop_size',
                                step: 1,
                                min: 1,
                                max: 20000,
                                type: 'number',
                                'aria-labelledby': 'input-slider',
                            }}
                        />
                    </FormControl>

                    {/*     EPOCHS     */}
                    <FormControl className={"midiform.formControl"}>
                        <InputLabel htmlFor="epochs">Epochs</InputLabel>
                        <Input
                            value={this.state.epochs}
                            onChange={this.handleChange}
                            inputProps={{
                                name: 'epochs',
                                id: 'epochs',
                                step: 1,
                                min: 1,
                                max: 20000,
                                type: 'number',
                                'aria-labelledby': 'input-slider',
                            }}
                        />
                    </FormControl>

                    {/*     MUT_RATE     */}
                    <FormControl className={"midiform.formControl"}>
                        <InputLabel htmlFor="mut_rate">Mutation%</InputLabel>
                        <Input
                            value={this.state.mut_rate}
                            onChange={this.handleChange}
                            inputProps={{
                                name: 'mut_rate',
                                id: 'mut_rate',
                                step: 0.001,
                                min: 0,
                                max: 1,
                                type: 'number',
                                'aria-labelledby': 'input-slider',
                            }}
                        />
                    </FormControl>
                    <br/>
                    <Button variant="text"
                            onClick={this.callScript}>Generate</Button> <br />
                </form>
                <br/>
                <Button variant="text"
                        onClick={() => this.props.handleClick({
                            remove: "MidiGenes",
                            add: <Projects />})}>Back</Button> <br />
            </div>
        )
    }
}

function Projects(props){
    return (
        <div className="projects">
            Projects <br/>
            <table align="center">
                <tbody>
                    <tr>
                        <th>Name</th>
                        <th>Description</th>
                        <th>Link</th>
                    </tr>
                    <tr>
                        <td>Midi-Genes</td>
                        <td>Generates melodies based on an evolutionary model.</td>
                        <td><a href="https://github.com/liqMix/Midi-Genes">GitHub</a>
                            <br/>
                            <Button variant="text" onClick={() => props.handleClick({
                                remove: "Projects",
                                add: <MidiGenes/>})}> Try it out! </Button>
                        </td>
                    </tr>
                </tbody>
            </table>
            <Button variant="text"
                    onClick={() => props.handleClick({
                        remove: "Projects",
                        add: <Links />})}>Back</Button> <br />
        </div>

    );
}

const Profile = () => (
        <div className="profile">
            <img src="web-noback.png" alt='liqMix' />
            <p>liqMix</p>
        </div>
);

function Links(props){
    return (
        <div className="links">
            <p>
                <Button variant="text" href="https://www.github.com/liqMix/">GitHub</Button> <br />
                <Button variant="text" href="https://www.linkedin.com/in/liqmix/">LinkedIn</Button> <br />
                <Button variant="text"
                        onClick={() => props.handleClick({
                            remove: "Links",
                            add: <Projects />})}> Projects </Button> <br/>
            </p>
        </div>
    );
}
// ========================================

ReactDOM.render(
    <Page>
        <Profile />
        <Links />
    </Page>,
  document.getElementById('root')
);